// Адаптивная галерея, увеличение изображений (Responsive-Lightbox)
$(function(){
	// Одиночное изображение
	$(".single-img").fancybox({
		speed	: 330,
		loop	: false,
		image	: {
			protect : true
		}
	});
	
	// Галерея
	$("[data-fancybox]").fancybox({
		speed	: 330,
		loop	: false,
		image	: {
			protect : true
		}
	});
	
	// Слайдер
	
	// Модальное (всплывающее) окно
	$(".modalbox").fancybox({
		"padding": 10, // отступ контента от краев окна
		"imageScale": false, // Принимает значение true - контент(изображения) масштабируется по размеру окна, или false - окно вытягивается по размеру контента. По умолчанию - TRUE
		"zoomOpacity": true,   // изменение прозрачности контента во время анимации (по умолчанию false)
		"zoomSpeedIn": 500,    // скорость анимации в мс при увеличении фото (по умолчанию 0)
		"zoomSpeedOut": 500,   // скорость анимации в мс при уменьшении фото (по умолчанию 0)
		"zoomSpeedChange": 1000, // скорость анимации в мс при смене фото (по умолчанию 0)
		"touch" : true,
		"scrolling": "auto",
		//"frameWidth":560,
		//"frameHeight":340,
		"overlayShow": true, // если true затеняят страницу под всплывающим окном. (по умолчанию true). Цвет задается в jquery.fancybox.css - div#fancy_overlay 
		"overlayOpacity" : 0.5,  // Прозрачность затенения  (0.3 по умолчанию)
		"hideOnContentClick": false, // Если TRUE  закрывает окно по клику по любой его точке (кроме элементов навигации). По умолчанию TRUE     
		"centerOnScroll": false // Если TRUE, окно центрируется на экране, когда пользователь прокручивает страницу      
	});
});

