// Инициализация библиотеки анимации
$(function(){
	var wow = new WOW ({
		boxClass:     'wow',      // класс, скрывающий элемент до момента отображения на экране (по умолчанию, wow)
		animateClass: 'animated', // класс для анимации элемента (по умолчанию, animated)
		offset:       0,          // расстояние в пикселях от нижнего края браузера до верхней границы элемента, необходимое для начала анимации (по умолчанию, 0)
		mobile:       true,       // включение/отключение WOW.js на мобильных устройствах (по умолчанию, включено)
		live:         true,       // поддержка асинхронно загруженных элементов (по умолчанию, включена)
		callback:     function(box) {
			// функция срабатывает каждый раз при старте анимации
			// аргумент box — элемент, для которого была запущена анимация
		},
		scrollContainer: null // селектор прокручивающегося контейнера (опционально, по умолчанию, window)
	});
	wow.init();
});

// Адаптианый аккордеон с отзывами
$(function(){
	! function(i) {
		var o, n;
		i(".title_block").on("click", function() {
			o = i(this).parents(".accordion_item"), n = o.find(".info"),
			o.hasClass("active_block") ? (o.removeClass("active_block"),
			n.slideUp()) : (o.addClass("active_block"), n.stop(!0, !0).slideDown(),
			o.siblings(".active_block").removeClass("active_block").children(
			".info").stop(!0, !0).slideUp())
		})
	}(jQuery);
});

// Кнопка вверх
$("#back-top").hide();
$(function () {
	$(window).scroll(function(){
		if ($(this).scrollTop() > 0) {
			$('#back-top').fadeIn();
		} else {
			$('#back-top').fadeOut();
		}
	});
	$('#back-top a').click(function(){
		$('body,html').animate({
			scrollTop: 0
		}, 800);
		return false;
	});
});

// Все изображения с realsrc загружаются, когда становятся видимыми
$(function(){
	// Вариант проверки, считающий элемент видимым, если он не более чем -1 страница назад или +1 страница вперед
	function isVisible(elem) {
		var coords = elem.getBoundingClientRect();
		var windowHeight = document.documentElement.clientHeight;
		var extendedTop = -windowHeight;
		var extendedBottom = 2 * windowHeight;
		// top visible || bottom visible
		var topVisible = coords.top > extendedTop && coords.top < extendedBottom;
		var bottomVisible = coords.bottom < extendedBottom && coords.bottom > extendedTop;

		return topVisible || bottomVisible;
	}
	// Показываем изображения
	function showVisible() {
		var imgs = document.getElementsByTagName('img');
		
		for (var i = 0; i < imgs.length; i++) {
			var img = imgs[i];

			var realsrc = img.getAttribute('realsrc');
			
			if (!realsrc) continue;

			if (isVisible(img)) {
				img.src = realsrc;
				img.setAttribute('realsrc', '');
			}
		}
	}
	window.onscroll = showVisible;
	showVisible();
});