//в этой функции отслеживается изменение чекбокса "я не робот"
$(document).on('change', '.form-vertical input:checkbox', function() {
	if($(this).is(':checked')){
		$(".form-vertical input[type=submit]").removeAttr('disabled');
		$('.form-vertical input[type=hidden].norobot').val('norobot_true');
	}
	else {
		$(".form-vertical input[type=submit]").attr('disabled','disabled');
		$('.form-vertical input[type=hidden].norobot').val('norobot_disabled');
	}
});

// Закрытие модального окна
$('.close-modal, .form-overlay').click(function (){
	$('.form-modal, .form-overlay').css({'opacity':'0', 'visibility':'hidden'});
	// Сброс всех полей формы обраной связи
	$(':input','.form-vertical').not(':button, :submit, :reset, :hidden').val('').removeAttr('checked').removeAttr('selected');
	$(".form-vertical input[type=submit]").attr('disabled','disabled');
});

// Аякс форма обратной связи
// Проверяет, какой ответ был получен
// И, в зависимости от ответа,
// Выводит информацию о статусе
// Отправки письма
jQuery(document).ready(function($){
	$(".form-vertical").submit(function() {
		var str = $(this).serialize();
		$.ajax({
			type: "POST",
			url: "assets/inc/send.php",
			data: str,
			success: function(msg) {
				if(msg == 'ok') {
					$('.form-modal, .form-overlay').css('opacity','1');
					$('.form-modal, .form-overlay').css('visibility','visible');
				}
				else {
					$('.form-modal .window').html('<h5>Ошибка</h5><p>Сообщение не отправлено, убедитесь в правильности заполнения полей</p>');
					$('.form-modal, .form-overlay').css('opacity','1');
					$('.form-modal, .form-overlay').css('visibility','visible');
				}
			}
		});
		return false;
	});
});

